<template>
  <div class="blog-card">
    <div v-show="editPost" class="icons">
      <div class="icon" @click="editBlogPost">
        <Edit class="edit" />
      </div>
      <div class="icon" @click="deletePost">
        <Delete class="delete" />
      </div>
    </div>
    <img :src="post.blogCoverPhoto" />
    <div class="info">
      <h4>{{ post.blogTitle }}</h4>
      <h6>
        Posted on:{{
          new Date(post.blogDate).toLocaleString("en-us", { dateStyle: "long" })
        }}
      </h6>
      <router-link
        class="link"
        :to="{ name: 'ViewBlog', params: { blogid: post.blogID } }"
        >View the Post <Arrow class="arrow" />
      </router-link>
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import Arrow from "./svg/Arrow.vue";
import Edit from "./svg/Edit.vue";
import Delete from "./svg/Delete.vue";

export default {
  components: { Arrow, Edit, Delete },
  name: "blog-card",
  props: {
    post: {
      type: Object,
    },
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();

    const editPost = computed(() => {
      return store.state.editPost;
    });

    const deletePost = () => {
      store.dispatch("deletePost", props.post.blogID);
    };
    const editBlogPost = () => {
      router.push({ name: "EditBlog", params: { blogid: props.post.blogID } });
    };

    return { editPost, deletePost, editBlogPost };
  },
};
</script>

<style scoped>
.blog-card {
  position: relative;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  background-color: #fff;
  min-height: 420px;
  transition: 0.5s ease all;
}
.blog-card:hover {
  transform: rotateZ(-1deg) scale(1.01);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.blog-card .icons {
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 99;
}
.blog-card .icons .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background-color: #fff;
  transition: 0.5s ease all;
}

.blog-card .icons .icon:hover {
  background-color: #d4f5f5;
}
.blog-card .icons .icon:hover .edit path {
  fill: #fff;
}

.blog-card .icons .icon:hover .delete path {
  fill: #fff;
}
.blog-card .icons .icon:nth-child(1) {
  margin-right: 15px;
}
.blog-card .icons .icon .edit,
.blog-card .icons .icon .delete {
  pointer-events: none;
  height: 20px;
  width: 20px;
}
.blog-card .icons .icon .edit {
  pointer-events: none;
  height: 20px;
  width: 20px;
}
.blog-card img {
  display: block;
  border-radius: 8px 8px 0 0;
  z-index: 1;
  width: 100%;
  min-height: 200px;
  object-fit: cover;
}
.info {
  display: flex;
  flex-direction: column;
  height: 50%;
  z-index: 3;
  padding: 32px 16px;
  color: #000;
}
.info h4 {
  padding-bottom: 8px;
  font-size: 20px;
  font-weight: 300;
}
.info h6 {
  font-weight: 400;
  font-size: 12px;
  padding-bottom: 16px;
}
.info .link {
  display: inline-flex;
  align-items: center;
  margin-top: auto;
  font-weight: 500;
  padding-top: 20px;
  font-size: 12px;
  padding-bottom: 4px;
  transition: 0.5s ease-in all;
}
.info .link .arrow {
  width: 10px;
}
.info .link:hover {
  color: rgba(48, 48, 48, 0.8);
}
.info .arrow {
  width: 10px;
}
</style>
